.container{
  
    border-radius: 6px; 
    margin-left: 0.7%;
    height: 322px;
    width: 255px;
    display: flex;
    flex-direction: row !important;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    display: inline-block;
    margin-top: 0.5%;
   
}

.container:hover {
   
  
    transform: translateY(-4px);
    border-color: 1px solid rgb(224, 134, 134);
   
}


.sub_container{
    background-color: white;
    width: 255px;
    height: 322px;
    border-radius: 10px 10px 10px 10px;
    margin-top: 2%;
    margin-left: 1.5% !important;
}

.image{
    width: auto;
    height: 200px; 
    background-color: white!important;
  
}

.image_inside{
    margin-left:23%;
     margin-top:17%
}

.value{
    display: flex;
    justify-content: space-between;
    margin-left: 3.5%;
    color: white;
}
.price{
    margin-top: 3%;
    color: rgb(119, 121, 140);
    font-size: 13px;
    margin-left: 1.5%;
  

}
.basket{
    margin-right: 5.5%;
    background-color:white;
    border: 2px solid rgb(247, 247, 247);
    border-radius: 18px;
    width: 40%;
    height: 36px !important;
    display: flex;
    justify-content: space-between!important;  
    color: white;
}

