.sub_calculator{
    display: flex;
    /* justify-content: space-around; */
    
}
.after_click{
     display: inline-block; 
     display: flex;
     /* justify-content: space-between; */
     /* background-color: rgb(0, 158, 127); */
     background-color: rgb(0, 158, 127);
     border-radius: 6px 0 0 6px;
     width: 120px !important;
     height: 36px !important;
     margin-left: 30% !important;
     margin-top: 1%;
     margin-left: 30% !important;
    
   
}
.after_click_hidden{
    display: inline-block; 
    display: flex;
    justify-content: space-between;
    background-color: rgb(0, 158, 127);
    border-radius: 6px 0 0 6px;
    width: 64px;
     height: 36px;
    margin-left: 30%;
    opacity: 0;
  
}



.drop_detailed {
    top: 12%;
    left: -0%;
    width: 100%;
 position: absolute;
    /* position: fixed;   */
    z-index: 105;
}



